<template>
	<section class="countdown-wrapper">
		<div class="banner-wrapper text-center">
			<label class="text-title font-poiret">28 December 2018</label>
			<label class="text-subtitle font-poiret">3:00PM @ Mahogany Farm, Taguig City</label>
		</div>
		<div class="countdown">
			<div class="clock-wrapper">
				<div class="clock">
					<div class="clock-block">
						<span class="clock-number" v-text="MM"></span>
						<span class="clock-text">MONTHS</span>
					</div>
					<div class="clock-block">
						<span class="clock-number" v-text="dd"></span>
						<span class="clock-text">DAYS</span>
					</div>
					<div class="clock-block">
						<span class="clock-number" v-text="hh"></span>
						<span class="clock-text">HOURS</span>
					</div>
					<div class="clock-block">
						<span class="clock-number" v-text="mm"></span>
						<span class="clock-text">MINUTES</span>
					</div>
					<div class="clock-block">
						<span class="clock-number" v-text="ss"></span>
						<span class="clock-text">SECONDS</span>
					</div>
				</div>
			</div>
			<!-- <h1>Countdown</h1> -->
		</div>
	</section>
</template>
<script>
	export default {
		data() {
			return {
				event: null,
				MM: '00',
				dd: '00',
				hh: '00',
				mm: '00',
				ss: '00',
				hDuration: ''
			}
		},
		mounted() {
			var d = new Date('12-31-2018 15:00:00 UTC+8');
			var event = window.moment(d.getTime()).unix();
			var current = window.moment(new Date().getTime()).unix();
			var diffTime = event - current;
			var duration = window.moment.duration(diffTime * 1000, 'milliseconds');
			this.hDuration = duration.humanize();
			var interval = 1000;

			var myInterval;
			if(diffTime > 0) {
				clearInterval(myInterval)
				myInterval = setInterval(function() {
					duration = window.moment.duration(duration.asMilliseconds() - interval, 'milliseconds');
					this.MM = window.moment.duration(duration).months();
					this.dd = window.moment.duration(duration).days();
					this.hh = window.moment.duration(duration).hours();
					this.mm = window.moment.duration(duration).minutes();
					this.ss = window.moment.duration(duration).seconds();

					this.MM = window._.trim(this.MM).length === 1 ? '0' + this.MM : this.MM;
					this.dd = window._.trim(this.dd).length === 1 ? '0' + this.dd : this.dd;
					this.hh = window._.trim(this.hh).length === 1 ? '0' + this.hh : this.hh;
					this.mm = window._.trim(this.mm).length === 1 ? '0' + this.mm : this.mm;
					this.ss = window._.trim(this.ss).length === 1 ? '0' + this.ss : this.ss;
				}.bind(this), interval)
			}

		}
	}
</script>


